import axios from '@/utils/axios'
import signMd5Utils from '@/utils/signMd5Utils'

const api = {
  user: '/user',
  role: '/role',
  service: '/service',
  userList: '/userList',
  permission: '/permission',
  permissionNoPager: '/permission/no-pager',
  orgTree: '/org/tree'
}

export default api

export function getUserList(parameter) {
  return axios({
    url: api.user,
    method: 'get',
    params: parameter
  })
}

export function getRoleList(parameter) {
  return axios({
    url: api.role,
    method: 'get',
    params: parameter
  })
}

export function getServiceList(parameter) {
  return axios({
    url: api.service,
    method: 'get',
    params: parameter
  })
}

export function getPermissions(parameter) {
  return axios({
    url: api.permissionNoPager,
    method: 'get',
    params: parameter
  })
}

export function getOrgTree(parameter) {
  return axios({
    url: api.orgTree,
    method: 'get',
    params: parameter
  })
}

// id == 0 add     post
// id != 0 update  put
export function saveService(parameter) {
  return axios({
    url: api.service,
    method: parameter.id === 0 ? 'post' : 'put',
    data: parameter
  })
}

export function getAction(url, parameter) {
  const sign = signMd5Utils.getSign(url, parameter)
  // 将签名和时间戳，添加在请求接口 Header
  const signHeader = {
    'X-Sign': sign,
    'X-TIMESTAMP': signMd5Utils.getDateTimeToString()
  }

  return axios({
    url,
    method: 'get',
    params: parameter,
    headers: signHeader
  })
}

export function getUserListNew(parameter) {
  return axios({
    url: api.userList,
    method: 'get',
    params: parameter
  })
}
