<template>
  <Card shadow>
    <Form :label-width="100" :label-colon="true">
      <Row :gutter="24">
        <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
          <FormItem label="用户名">
            <Input placeholder="请输入用户名" />
          </FormItem>
        </Col>
        <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
          <FormItem label="手机号">
            <Input placeholder="请输入手机号" />
          </FormItem>
        </Col>
        <template v-if="advanced">
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="邮箱">
              <Input placeholder="请输入邮箱" />
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="性别">
              <Select placeholder="请选择性别" clearable>
                <Option value="1">男</Option>
                <Option value="2">女</Option>
              </Select>
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="登录账号">
              <Input placeholder="请输入登录账号" />
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="用户ID">
              <Input placeholder="请输入用户ID" />
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="状态" clearable>
              <Select placeholder="请选择状态">
                <Option value="1">正常</Option>
                <Option value="2">冻结</Option>
                <Option value="3">关闭</Option>
              </Select>
            </FormItem>
          </Col>
          <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
            <FormItem label="创建时间">
              <DatePicker style="width: 100%" placeholder="请选创建时间" />
            </FormItem>
          </Col>
        </template>
        <Col :xl="8" :lg="8" :md="12" :sm="24" :xs="24">
          <FormItem :label-width="100">
            <Space>
              <Button icon="md-search" type="primary" @click="getUserList" :loading="loading">查询</Button>
              <Button icon="md-refresh" @click="getUserList">重置</Button>
              <a @click="toggleAdvanced">
                {{ advanced ? '收起' : '展开' }}
                <Icon :type="advanced ? 'ios-arrow-up' : 'ios-arrow-down'" />
              </a>
            </Space>
          </FormItem>
        </Col>
      </Row>
    </Form>
    <div>
      <Space>
        <Button type="primary" icon="md-add" ghost @click="addUser">添加用户</Button>
        <Button type="error" icon="md-trash" ghost @click="delUsers">批量删除</Button>
      </Space>
      <div class="ivu-inline-block ivu-fr">
        <Tag color="blue">工具栏</Tag>
      </div>
      <Table
        :columns="columns"
        :data="data"
        :loading="loading"
        class="ivu-mt"
        @on-selection-change="select => (this.selectedRows = select)"
        @on-cell-click="cellClick"
      >
        <template #username="{ row }">
          <Avatar shape="square" :src="row.avatar" size="small" />
          {{ row.username }}
        </template>
        <template #sex="{ row }">
          <Tag :color="row.sex === '1' ? 'blue' : 'red'">{{ row.sex === '1' ? '男' : '女' }}</Tag>
        </template>
        <template #status="{ row }">
          <Badge :status="row.status === '1' ? 'success' : 'error'" :text="row.status === '1' ? '正常' : '冻结'" />
        </template>
        <template #action="{ row }">
          <a @click="editUser(row)">编辑</a>
          <Divider type="vertical" />
          <a v-color="'red'" @click="removeUser(row)">删除</a>
        </template>
      </Table>
      <div class="ivu-mt ivu-text-center">
        <Page show-sizer show-elevator :total="total" @on-change="changePage" @on-page-size-change="changePageSize" />
      </div>
    </div>
    <DialogContainer :width="560" :modelValue="visible" :title="title" @on-visible-change="val => (visible = val)">
      <Form ref="form" :model="form" :rules="rule" :label-colon="true" label-position="top">
        <Row :gutter="32">
          <template v-if="form && form.id">
            <Col :span="10" class="ivu-text-center">
              <Avatar :src="form.avatar" :size="64" shape="square" />
              <Upload v-if="!readOnly" action="//jsonplaceholder.typicode.com/posts/" class="ivu-mt-8">
                <Button type="text"
                  >修改
                  <Icon type="ios-create-outline" />
                </Button>
              </Upload>
            </Col>
            <Col :span="14">
              <div>用户ID：{{ form.id }}</div>
              <div class="ivu-mt-8">登录账号：{{ form.account }}</div>
              <div class="ivu-mt-8">创建时间：{{ form.createTime }}</div>
            </Col>
            <Divider />
          </template>
          <Col :span="12">
            <FormItem label="用户名">
              <Input v-model="form.username" :readonly="readOnly" placeholder="请输入用户名，必填" />
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="手机号">
              <Input v-model="form.phone" :readonly="readOnly" placeholder="请输入手机号，必填" />
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="邮箱">
              <Input v-model="form.email" :readonly="readOnly" placeholder="请输入邮箱，必填" />
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="性别" clearable>
              <Select v-model="form.sex" :readonly="readOnly" placeholder="请选择性别，必填">
                <Option value="1">男</Option>
                <Option value="2">女</Option>
              </Select>
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="状态" clearable>
              <Select v-model="form.status" :readonly="readOnly" placeholder="请选择状态">
                <Option value="1">正常</Option>
                <Option value="2">冻结</Option>
                <Option value="3">关闭</Option>
              </Select>
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="角色分配">
              <Select v-model="form.role" :readonly="readOnly" placeholder="请选择角色" multiple :max-tag-count="1">
                <Option value="1">
                  ROLE_USER
                  <span style="color: #c5c8ce">普通用户</span>
                </Option>
                <Option value="2">
                  ROLE_ADMIN
                  <span style="color: #c5c8ce">管理员</span>
                </Option>
                <Option value="3">
                  ROLE_OP
                  <span style="color: #c5c8ce">运营</span>
                </Option>
              </Select>
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="登录账号">
              <Input v-model="form.account" :readonly="readOnly" placeholder="请输入登录账号，必填" />
            </FormItem>
          </Col>
          <Divider />
          <Col :span="12">
            <FormItem label="地区">
              <Region v-model="form.area" :readonly="readOnly" placeholder="请选择地区" />
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="生日">
              <DatePicker v-model="form.birthday" :readonly="readOnly" placeholder="请选择日期" />
            </FormItem>
          </Col>
          <Col :span="24">
            <FormItem label="个人介绍">
              <Input v-model="form.desc" :readonly="readOnly" type="textarea" :rows="3" placeholder="请输入个人介绍" />
            </FormItem>
          </Col>
        </Row>
      </Form>
    </DialogContainer>
  </Card>
</template>

<script>
  import { Region } from '@/components'
  import { getUserListNew } from '@/api/manage'

  export default {
    name: 'User',
    components: { Region },
    data() {
      return {
        query: {
          pageNo: 1,
          pageSize: 10
        },
        advanced: false,
        loading: false,
        columns: [
          {
            type: 'selection',
            align: 'center',
            width: 60
          },
          {
            title: '用户名',
            key: 'username',
            slot: 'username',
            sortable: true,
            minWidth: 140
          },
          {
            title: '登录账号',
            key: 'account',
            sortable: true,
            minWidth: 120
          },
          {
            title: '手机号',
            key: 'phone',
            minWidth: 140
          },
          {
            title: '邮箱',
            key: 'email',
            minWidth: 180
          },
          {
            title: '性别',
            key: 'sex',
            slot: 'sex',
            minWidth: 100
          },
          {
            title: '状态',
            key: 'status',
            slot: 'status',
            minWidth: 100
          },
          {
            title: '创建时间',
            key: 'createTime',
            sortable: true,
            minWidth: 180
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center',
            fixed: 'right',
            minWidth: 120
          }
        ],
        data: [],
        total: 0,
        visible: false,
        selectedRows: [],
        form: {},
        rule: {},
        title: '',
        readOnly: false
      }
    },
    created() {
      this.getUserList()
    },
    methods: {
      getUserList() {
        this.loading = true
        const req = { ...this.query }
        getUserListNew(req).then(res => {
          this.data = res.data
          this.total = res.totalCount
          this.loading = false
        })
      },
      changePage(pageNo) {
        this.query.pageNo = pageNo
        this.getUserList()
      },
      changePageSize(pageSize) {
        this.query.pageSize = pageSize
        this.getUserList()
      },
      editUser(record) {
        this.form = record
        this.title = '编辑用户'
        this.readOnly = false
        this.visible = true
      },
      removeUser(record) {
        this.$Modal.confirm({
          title: '删除确认',
          content: `您确认要删除用户<strong>${record.username}</strong>？`,
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.error('抱歉，您没有该权限')
            }, 800)
          },
          onCancel: () => {
            this.$Message.info('点击了取消')
          }
        })
      },
      toggleAdvanced() {
        this.advanced = !this.advanced
      },
      addUser() {
        this.form = {}
        this.title = '添加用户'
        this.readOnly = false
        this.visible = true
      },
      delUsers() {
        if (!this.selectedRows.length) {
          this.$Message.error('您没有选择用户')
          return
        }
        this.$Modal.confirm({
          title: '删除确认',
          content: `您确认要删除用户<strong>${this.selectedRows.map(o => o.username).join(',')}</strong>？`,
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.error('抱歉，您没有该权限')
            }, 800)
          },
          onCancel: () => {
            this.$Message.info('点击了取消')
          }
        })
      },
      cellClick(row, column, data, event) {
        if (!data) return
        this.form = row
        this.title = '用户信息'
        this.readOnly = true
        this.visible = true
      }
    }
  }
</script>
