<template>
  <Card shadow>
    <Button type="primary" icon="md-add" ghost class="ivu-mr-8" @click="addRole">添加角色</Button>
    <Button type="error" icon="md-trash" ghost @click="delRoles">批量删除</Button>
    <div class="ivu-inline-block ivu-fr">
      <Tag color="blue">工具栏</Tag>
    </div>
    <Table :columns="columns" :data="data" :loading="loading" class="ivu-mt" @on-selection-change="select => (this.selectedRows = select)">
      <template #defaultRole="{ row }">
        <Tag :color="row.defaultRole === '1' ? 'green' : 'red'">{{ row.defaultRole === '1' ? '是' : '否' }}</Tag>
      </template>
      <template #action="{ row }">
        <a @click="editRole(row)">菜单权限</a>
        <Divider type="vertical" />
        <a @click="editRole(row)">编辑</a>
        <Divider type="vertical" />
        <a @click="removeRole(row)">删除</a>
      </template>
    </Table>
    <div class="ivu-mt ivu-text-center">
      <Page show-sizer show-elevator :total="total" @on-change="changePage" @on-page-size-change="changePageSize" />
    </div>
    <DialogContainer :width="520" :modelValue="visible" :title="title" @on-visible-change="val => (visible = val)">
      <Form ref="form" :model="form" :rules="rule" :label-colon="true" label-position="top">
        <FormItem label="角色名称">
          <Input v-model="form.name" placeholder="请输入角色名称，必填" />
        </FormItem>
        <FormItem label="说明">
          <Input v-model="form.desc" placeholder="请输入说明" />
        </FormItem>
        <FormItem>
          <template #label>
            是否默认角色
            <Tooltip content="开启后，新注册用户默认拥有该角色" placement="top">
              <Icon type="md-help-circle" />
            </Tooltip>
          </template>
          <i-switch v-model="form.defaultRole" true-value="1" false-value="0">
            <template #open>
              <Icon type="md-checkmark" />
            </template>
            <template #close>
              <Icon type="md-close" />
            </template>
          </i-switch>
        </FormItem>
      </Form>
    </DialogContainer>
  </Card>
</template>

<script>
  export default {
    name: 'Role',
    data() {
      return {
        query: {
          pageNo: 1,
          pageSize: 10
        },
        loading: false,
        columns: [
          {
            type: 'selection',
            align: 'center',
            width: 60
          },
          {
            title: '角色名称',
            key: 'name',
            sortable: true,
            minWidth: 140
          },
          {
            title: '说明',
            key: 'desc',
            minWidth: 120
          },
          {
            title: '创建时间',
            key: 'createTime',
            sortable: true,
            minWidth: 180
          },
          {
            title: '更新时间',
            key: 'updateTime',
            sortable: true,
            minWidth: 180
          },
          {
            title: '是否默认角色',
            key: 'defaultRole',
            slot: 'defaultRole',
            minWidth: 140
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center',
            fixed: 'right',
            minWidth: 200
          }
        ],
        data: [],
        total: 0,
        form: {},
        rule: {},
        title: '',
        visible: false,
        selectedRows: []
      }
    },
    created() {
      this.getRoleList()
    },
    methods: {
      getRoleList() {
        setTimeout(() => {
          this.data = [
            {
              id: 2000000001,
              name: 'ROLE_USER',
              desc: '普通用户',
              createTime: '2020-01-01 12:01:25',
              updateTime: '2020-01-02 13:10:33',
              defaultRole: '1'
            },
            {
              id: 2000000002,
              name: 'ROLE_OP',
              desc: '运营用户',
              createTime: '2020-01-02 08:12:43',
              updateTime: '2020-01-04 22:12:28',
              defaultRole: '0'
            },
            {
              id: 2000000003,
              name: 'ROLE_ADMIN',
              desc: '管理员用户',
              createTime: '2020-01-03 12:44:19',
              updateTime: '2020-01-06 14:02:22',
              defaultRole: '0'
            }
          ]
        }, 500)
      },
      addRole() {
        this.form = { defaultRole: '0' }
        this.title = '添加角色'
        this.visible = true
      },
      delRoles() {
        if (!this.selectedRows.length) {
          this.$Message.error('您没有选择角色')
          return
        }
        this.$Modal.confirm({
          title: '删除确认',
          content: `您确认要删除角色<br/><strong>${this.selectedRows.map(o => o.name).join(',')}</strong>？`,
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.error('抱歉，您没有该权限')
            }, 800)
          },
          onCancel: () => {
            this.$Message.info('点击了取消')
          }
        })
      },
      editRole(row) {
        this.form = row
        this.title = '编辑角色'
        this.visible = true
      },
      removeRole(row) {
        this.$Modal.confirm({
          title: '删除确认',
          content: `您确认要删除角色<strong>${row.name}</strong>？`,
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.error('抱歉，您没有该权限')
            }, 800)
          },
          onCancel: () => {
            this.$Message.info('点击了取消')
          }
        })
      },
      changePage(pageNo) {
        this.query.pageNo = pageNo
        this.getRoleList()
      },
      changePageSize(pageSize) {
        this.query.pageSize = pageSize
        this.getRoleList()
      }
    }
  }
</script>

<style scoped></style>
