<template>
  <Row :gutter="24">
    <Col :xs="24" :sm="24" :md="24" :lg="24" :xl="10">
      <Card shadow>
        <template #title>
          <Dropdown>
            <Button type="primary" ghost class="ivu-mr"
              >添加菜单
              <Icon type="ios-arrow-down" />
            </Button>
            <template #list>
              <DropdownMenu>
                <DropdownItem>添加顶栏菜单</DropdownItem>
                <DropdownItem :disabled="!Object.keys(selectedMenu).length">添加子菜单</DropdownItem>
              </DropdownMenu>
            </template>
          </Dropdown>
          <Button type="primary" ghost class="ivu-mr" icon="md-list" @click="collapseOrExpand">全部{{ collapse ? '收起' : '展开' }}</Button>
          <Button type="error" ghost class="ivu-mr" icon="md-trash" @click="deleteMenus">批量删除</Button>
        </template>
        <div class="ivu-p-8">
          <Input suffix="ios-search" placeholder="输入菜单名称搜索" />
        </div>
        <Tree
          :data="treeData"
          show-checkbox
          @on-select-change="handleSelectMenu"
          @on-check-change="handleCheckedMenu"
          @on-contextmenu="handleContextMenu"
        >
          <template #contextMenu>
            <DropdownItem @click="menuEdit">编辑</DropdownItem>
            <DropdownItem @click="menuEdit">添加子菜单</DropdownItem>
            <DropdownItem @click="menuDelete" divided style="color: #ed4014">删除</DropdownItem>
          </template>
        </Tree>
      </Card>
    </Col>
    <Col :xs="24" :sm="24" :md="24" :lg="24" :xl="14">
      <Card shadow>
        <template #title>
          <div class="ivu-menu-head">
            <Avatar size="small" icon="md-apps" style="color: #1890ff; background: #e6f7ff" />
            <span class="ivu-pl-8">编辑菜单{{ Object.keys(selectedMenu).length ? '：' + selectedMenu.title : '' }}</span>
          </div>
        </template>
        <Empty v-show="!Object.keys(selectedMenu).length" desc="请选择一项菜单编辑"></Empty>
        <Form v-if="Object.keys(selectedMenu).length" :label-width="100" :label-colon="true" ref="form" :model="form" :rules="rule">
          <FormItem label="类型">侧边栏菜单</FormItem>
          <FormItem label="标题">
            <Input v-model="form.title" placeholder="请输入标题，必填" />
          </FormItem>
          <FormItem label="副标题">
            <Input v-model="form.subtitle" placeholder="请输入副标题" />
          </FormItem>
          <FormItem label="路径">
            <Input v-model="form.path" placeholder="请输入路径，必填" />
          </FormItem>
          <FormItem label="打开方式">
            <RadioGroup v-model="form.openWay">
              <Radio label="0">当前窗口</Radio>
              <Radio label="1">新窗口</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem>
            <template #label>
              分割线
              <Tooltip content="菜单折叠时是否显示分割线" placement="top">
                <Icon type="md-help-circle" />
              </Tooltip>
            </template>
            <i-switch v-model="form.divider" true-value="1" false-value="0">
              <template #open>
                <Icon type="md-checkmark" />
              </template>
              <template #close>
                <Icon type="md-close" />
              </template>
            </i-switch>
          </FormItem>
          <FormItem label="图标">
            <IconSelect v-model="form.icon" placement="top" clearable placeholder="请选择图标" />
          </FormItem>
          <FormItem>
            <template #label>
              菜单权限
              <Tooltip content="菜单权限" placement="top">
                <Icon type="md-help-circle" />
              </Tooltip>
            </template>
            <Input v-model="form.auth" placeholder="权限，逗号分隔" />
          </FormItem>
          <FormItem>
            <Space :size="16">
              <Button @click="resetMenu">重置</Button>
              <Button type="primary" @click="resetMenu">保存</Button>
            </Space>
          </FormItem>
        </Form>
      </Card>
    </Col>
  </Row>
</template>

<script>
  import { mapState } from 'vuex'
  import { IconSelect } from '@/components'
  import { convertRoutePath } from '@/utils/routeConvert'

  export default {
    name: 'Menu',
    components: { IconSelect },
    data() {
      return {
        form: {
          title: '',
          subtitle: '',
          path: '',
          openWay: '0',
          divider: '0',
          icon: '',
          auth: ''
        },
        rule: {},
        treeData: [],
        contextData: {},
        collapse: false,
        checkedMenus: [],
        selectedMenu: {}
      }
    },
    created() {
      this.getTreeData()
    },
    computed: {
      ...mapState({
        mainMenu: state => state.auth.addRouters
      })
    },
    methods: {
      getTreeData() {
        const routes = convertRoutePath(this.mainMenu)
        this.formatMenus(this.treeData, routes)
      },
      formatMenus(arr, menus) {
        for (const menu of menus) {
          const item = {
            title: menu.meta.title,
            path: menu.path,
            openWay: '0',
            divider: '0',
            icon: menu.meta.icon,
            expand: !!menu.children && this.collapse,
            contextmenu: true,
            checked: false,
            children: []
          }
          menu.children && this.formatMenus(item.children, menu.children)
          arr.push(item)
        }
      },
      handleSelectMenu(nodeArray, selected) {
        this.selectedMenu = selected.selected ? selected : {}
        this.form = { ...this.selectedMenu }
      },
      handleCheckedMenu(nodeArray, checked) {
        this.checkedMenus = [...nodeArray]
      },
      handleContextMenu(data) {
        this.contextData = data
      },
      menuEdit() {
        this.$Message.info(`编辑了 ${this.contextData.title}`)
      },
      menuDelete() {
        this.$Message.error(`删除了 ${this.contextData.title}`)
      },
      collapseOrExpand() {
        this.collapse = !this.collapse
        this.treeData = []
        this.getTreeData()
      },
      deleteMenus() {
        if (!this.checkedMenus.length) {
          this.$Message.error('您没有勾选菜单')
          return
        }
        this.$Modal.confirm({
          title: '删除确认',
          content: `您确认要删除菜单<strong>${this.checkedMenus.map(o => o.title).join(',')}</strong>？`,
          loading: true,
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
              this.$Message.error('抱歉，您没有该权限')
            }, 800)
          },
          onCancel: () => {
            this.$Message.info('点击了取消')
          }
        })
      },
      resetMenu() {
        this.form = { ...this.selectedMenu }
      }
    }
  }
</script>

<style lang="less" scoped>
  .ivu-menu-head {
    display: flex;
    align-items: center;
    height: 32px;
  }
</style>
